import { Component, OnInit } from '@angular/core';
import { LoginService } from '../login.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Constants } from '../constants';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username:String;
  password:String;
  constructor(public loginService:LoginService,public snack:MatSnackBar) { }

  ngOnInit() {
  }
  login()
  {
    console.log(this.username,this.password);
    this.loginService.postLogin(this.username,this.password).subscribe(res=>{
      Constants.setToken(res.token);
      this.snack.open('Login Successful', 'OK', {duration: 2000});
    });
  }
}
