import { Component, ViewChild } from '@angular/core';
import { Constants } from './constants';
import { LoginService } from './login.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router, RouterLink, Route } from '@angular/router';
import { MatSidenav, MatSidenavContainer } from '@angular/material/sidenav';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'ark';
  events: string[] = [];
  opened: boolean;
  isloggedIn = false;
  username:String;
  password:String;

  constructor(public loginService:LoginService,public snack:MatSnackBar, public route:Router) { }
  
  @ViewChild('drawer', {static:true}) public sidenav: MatSidenav;

  toggle()
  {
    this.isloggedIn = !this.isloggedIn;
  }

  login()
  {
    this.loginService.postLogin(this.username,this.password).subscribe(res=>{
      window.localStorage.setItem('token',res.token);
      this.snack.open('Login Successful', 'OK', {duration: 2000});
      this.sidenav.close();
    });

  }

  explore() {
      if (this.loginService.isLoggedIn()) {
        this.route.navigate(['/explore']);
      }
      else {
        this.sidenav.open();
      }
  }
  isLoggedIn() {
    return this.loginService.isLoggedIn();
  }

  logout() {
    this.loginService.logout();
    this.route.navigate(['/home']);
  }
}
